.dialog {
    background-color: var(--form-color);
    max-width: 25vw;
    padding: 2vh 1vw;
    border-radius: 1vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.dialog h3 {
    font-size: 1.6rem;
    margin-bottom: 0.3em;
}

.dialog p {
    font-size: 1.2rem;
    font-weight: 400;
}

.dialog span {
    white-space: nowrap;
}

.dialog button {
    font-size: 1.2rem;
    width: 50%;
    margin-top: 1em;
    padding: 0.4em;
    border-radius: 1em;
    background-color: transparent;
}

.errorDialog {
    border: 1px solid red;
}

.loading {
    /*filter: invert(90%);*/
    width: 15vmin;
    animation: spin 1.5s linear infinite;
    transition: 0s;
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
