:root {
    --padding: 1em;
}

.inputColumn {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.inputWrapper {
    width: 100%;
    height: 2.5rem;
    background-color: var(--input-color);
    box-sizing: border-box;
    border: 1px var(--main-text-color) solid;
    border-radius: 1em;
    padding: 0 var(--padding);
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
}

.disabledWrapper {
    background-color: rgba(100, 100, 100, 0.2);
}

.input {
    width: 100%;
    border: none;
    background-color: transparent;
    font-size: 1rem;
}

.errorWrapper {
    border: red 1px solid;
}

.dropdownErrorWrapper {
    border: red 1px solid;
    border-bottom: 0;
}

.dropdownError {
    border: red 1px solid;
    border-top: 0;
}

.dropdownBorder {
    border: 1px var(--main-text-color) solid;
}

.errorInput {
    width: 100%;
    margin-right: 5%;
}

.error {
    width: 10%;
    max-height: 1.5em;
    object-fit: contain;
}

.errorMessage {
    margin-top: 0.2vh;
    color: red;
    font-size: 0.8rem;
    align-self: start;
    text-align: start;
}

.inputWrapper > input:focus {
    outline: none;
}

.listInput {
    width: 100%;
    margin-right: 5%;
    background-color: transparent;
    border: none;
    font-size: 1rem;
}

.unEditable {
    caret-color: transparent;
}

.arrow {
    width: 1.5em;
    height: 70%;
    object-fit: contain;
    transition: transform 0.5s ease-in-out;
}

.arrowUp {
    transform: rotate(180deg);
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown_content {
    display: none;
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    overflow: auto;
    z-index: 1;
    background-color: var(--input-color);
    border-top: none;
    border-radius: 0 0 var(--padding) var(--padding);
}

.scrollable {
    width: 100%;
    min-height: var(--padding);
    max-height: calc(6.2em + 15px);
    overflow: auto;
    box-sizing: border-box;
}

.hoveredOption {
    background-color: var(--main-background-color);
}

.scrollable::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

.scrollable::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.1);
}

.scrollable::-webkit-scrollbar-track,
.scrollable::-webkit-scrollbar-corner {
    background-color: var(--main-background-color);
}

.dropdown_option {
    width: 100%;
    box-sizing: border-box;
    padding: 0.4em var(--padding);
    text-align: start;
    font-size: 1em;
}

.showContent {
    display: block;
    border-top-width: 0;
}

.hoveredWrapper {
    border-bottom: 0;
    border-radius: var(--padding) var(--padding) 0 0;
}
