.aspectRatioBox {
    width: 100%;
    position: relative;
}

.ratio120 {
    padding-top: 120%;
}

.coveredImage {
    position: relative;
    top: 0;
    left: 0;
    border-radius: 1vw;
    overflow: hidden;
}

.mainWrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);
    cursor: grab;
}

.mainSlider {
    display: flex;
    width: 100%;
    height: 100%;
}

.mainSlider img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.arrow {
    height: 14%;
    width: auto;
    position: absolute;
    top: 43%;
}

.arrow:hover {
    cursor: pointer;
}

.arrowLeft {
    left: 0;
    rotate: 90deg;
}

.arrowRight {
    right: 0;
    rotate: -90deg;
}

.smallWrapper {
    width: 100%;
    margin-top: 3vh;
    padding-bottom: 1%;
    padding-right: 1%;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    border-radius: 1vw;
}

.smallSlider {
    display: flex;
    gap: 5%;
    width: fit-content;
    transition: transform ease-in-out;
}

.blurImage {
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    width: 100%;
    height: 100%;
}

.cover {
    width: 30%;
    flex-shrink: 0;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
}

.cover > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (max-width: 768px) {
    .cover {
        width: 20%;
    }

    .smallWrapper > div {
        gap: 6%;
    }

    .ratio120 {
        padding-top: 100%;
    }
}